/* mulish-regular - latin */
@font-face {
    font-family: "Mulish";
    font-style: normal;
    font-weight: 400;
    src: url("../fonts/mulish-v7-latin-regular.woff2") format("woff2"),
        url("../fonts/mulish-v7-latin-regular.woff") format("woff");
}

/* mulish-700 - latin */
@font-face {
    font-family: "Mulish";
    font-style: normal;
    font-weight: 700;
    src: url("../fonts/mulish-v7-latin-700.woff2") format("woff2"),
        url("../fonts/mulish-v7-latin-700.woff") format("woff");
}
